.backdrop-products {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  background: rgba(0, 0, 0, 0.2);
  transition: opacity var(--duration-cubic), visibility var(--duration-cubic);
  overflow-y: scroll;

  z-index: 100000000;

  &.is-hidden {
    opacity: 0;
    pointer-events: none;
  }

  &.is-hidden .modal__products {
    transform: translateX(-50%), scale(0.9);
  }
}

.modal__products {
  width: 280px;
  background: var(--main-white-color);
  border: 2px solid var(--yello-color);
  border-radius: 10px;
  padding: 28px 20px 32px;
  position: absolute;
  top: 10px;
  left: 50%;
  transform: translateX(-50%) scale(1);
  transition: transform var(--main-transition);

  .backdrop.is-hidden & {
    transform: translateX(-50%);
  }

  @media screen and (min-width: 768px) {
    width: 568px;
    padding: 32px 40px;
  }
}

.modal-products__button {
  border: none;
  background: transparent;
  position: absolute;
  top: 28px;
  right: 20px;

  &:hover,
  &:focus {
    transform: scale(1.2);
    fill: var(--yello-color);
  }

  @media screen and (min-width: 768px) {
    top: 20px;
  }
}

.modal-products__title {
  font-family: var(--Seymour);
  font-size: 20px;
  line-height: 1.4;
  text-align: center;
  color: var(--main-black-color);
}

.modal-products__img-wrap {
  position: relative;
  margin: 22px auto 0;
  width: 131px;
  height: 131px;
  border-radius: 50%;

  @media screen and (min-width: 768px) {
    margin: 24px auto 0;
  }
}

.modal-products__img-wrap--icecream {
  background: linear-gradient(
      0deg,
      rgba(156, 190, 143, 0.7),
      rgba(156, 190, 143, 0.7)
    ),
    #d9d9d9;
  box-shadow: 0px 5.26027px 19.726px rgba(10, 136, 23, 0.25);
}

.modal-products__img-wrap--ice-coffee {
  background: linear-gradient(
      0deg,
      rgba(248, 210, 78, 0.7),
      rgba(248, 210, 78, 0.7)
    ),
    #d9d9d9;
  box-shadow: 0px 5.40397px 20.2649px rgba(153, 114, 23, 0.25);
}

.modal-products__img-wrap--milkshakes {
  background: linear-gradient(
      0deg,
      rgba(213, 117, 63, 0.7),
      rgba(213, 117, 63, 0.7)
    ),
    #d9d9d9;
  box-shadow: 0px 5.41333px 20.3px rgba(0, 0, 0, 0.25);
}

.modal-products__img {
  position: absolute;
  bottom: 7px;
  width: 131px;
  height: 131px;
}

.modal-products__components-list {
  margin-top: 24px;
}

.components-list {
  display: flex;
  justify-content: space-between;

  @media screen and (min-width: 768px) {
    gap: 20px;
    justify-content: center;
  }
}

.components-list__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 53px;
  height: 72px;
  border-radius: 40px;

  @media screen and (min-width: 768px) {
    width: 65px;
    height: 90px;
  }
}

.components-list__item--icecream {
  border: 2px solid var(--calories-stroke-color);
}

.components-list__item--ice-coffee {
  border: 2px solid var(--ice-coffee-color);
}

.components-list__item--milkshakes {
  border: 2px solid var(--milkshakes-color);
}

.components-list__circle {
  position: relative;
  top: -1px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 43px;
  height: 43px;

  border-radius: 50%;

  font-weight: 500;
  font-size: 10px;
  line-height: 2.8;

  @media screen and (min-width: 768px) {
    width: 53px;
    height: 53px;
    font-size: 12px;
    line-height: 2.33;
  }
}

.components-list__circle--icecream {
  background-color: var(--calories-stroke-color);
  border: 2px solid var(--calories-stroke-color);
  box-shadow: 0px 6px 20px rgba(10, 136, 23, 0.25);
}

.components-list__circle--ice-coffee {
  background-color: var(--ice-coffee-color);
  border: 2px solid var(--ice-coffee-color);
  box-shadow: 0px 5.40397px 20.2649px rgba(153, 114, 23, 0.25);
}

.components-list__circle--milkshakes {
  background-color: var(--milkshakes-color);
  border: 2px solid var(--milkshakes-color);
  box-shadow: 0px 5.41333px 20.3px rgba(0, 0, 0, 0.25);
}

.components-list__text {
  margin-top: 8px;
  font-weight: 400;
  font-size: 10px;
  line-height: 1.2;

  @media screen and (min-width: 768px) {
    margin-top: 7px;
    font-size: 12px;
  }
}

.modal-products__subtitle {
  margin-top: 39px;
  margin-bottom: 24px;
  text-align: center;
  font-weight: 500;
  font-size: 20px;
  line-height: 1.4;
  color: var(--main-black-color);

  @media screen and (min-width: 768px) {
    margin-top: 48px;
  }
}

.ingredients-list__item {
  display: flex;
  justify-content: space-between;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.76;
  letter-spacing: 0.04em;
  color: var(--main-black-color);
}

.ingredients-list__item:not(:last-child) {
  margin-bottom: 11px;
}

.modal-products__form {
  margin-top: 48px;
  text-align: center;

  @media screen and (min-width: 768px) {
    margin-top: 49px;
    width: 368px;
    margin-left: auto;
    margin-right: auto;
  }
}

.modal-products__input,
.modal-products__comment {
  width: 100%;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.4;
  color: var(--main-black-color);
}

.modal-products__input {
  margin-bottom: 16px;
  height: 42px;
  padding: 11px;
  background: rgba(248, 210, 78, 0.1);
  border: 1px solid #84a178;
  border-radius: 10px;
}

.modal-products__comment {
  height: 84px;
  padding: 11px;
  background: rgba(248, 210, 78, 0.1);
  border: 1px solid #84a178;
  border-radius: 10px;
  resize: none;
}

.modal-products__btn {
  margin-top: 32px;
  font-weight: 600;
  font-size: 14px;
  line-height: 1.2;
  color: var(--main-black-color);
  min-width: 132px;
  justify-content: center;
}
